import { useState, useEffect, useCallback } from 'react';
import { useSelector } from 'react-redux';
import Protobuf from '../../../../protobuf/Protobuf';
import type Proxy from '../../../../protobuf/Proxy';
import logger from '../../../../utils/logger';
import modes from '../../../../utils/modes';
import {
  bytesToString,
  CommonHookProps,
  EmbeddedIdEnum,
  EmbeddedValue,
  LinkedSelectedOption,
  ResetType,
  stringToBytes,
  VARENUM,
} from '../../common';

interface RangeValueBase {
  rangeClass:
    | EmbeddedIdEnum.RangeClassFailHighEId
    | EmbeddedIdEnum.RangeClassFailLowEId
    | EmbeddedIdEnum.RangeClassWarnHighEId
    | EmbeddedIdEnum.RangeClassWarnLowEId;
  inspectionID: number;
  value: number;
  equationId: number;
  embeddedValueObjectID?: number;
}

interface DirectRangeValue extends RangeValueBase {
  option: LinkedSelectedOption.DirectValue;
}

interface IndirectRangeValue extends RangeValueBase {
  option: LinkedSelectedOption.IndirectValue;
  indirectObjectId?: number;
}

interface FormulaRangeValue extends RangeValueBase {
  option: LinkedSelectedOption.Formula;
  formula?: string;
}

export type RangeValue = DirectRangeValue | IndirectRangeValue | FormulaRangeValue;

export interface Range {
  failHigh?: RangeValue;
  failLow?: RangeValue;
  warnHigh?: RangeValue;
  warnLow?: RangeValue;
}

export const MIN_RANGE_VALUE = -1700000;
export const MAX_RANGE_VALUE = 1700000;

export function clampRangeValue(val: number) {
  return Math.max(MIN_RANGE_VALUE, Math.min(MAX_RANGE_VALUE, val));
}

export function compareRangeValues(a: RangeValue | undefined, b: RangeValue | undefined) {
  if (a === b) {
    return true;
  }
  if (!a || !b) {
    return false;
  }
  if (a.option !== b.option || a.value !== b.value) {
    return false;
  }
  switch (a.option) {
    case LinkedSelectedOption.IndirectValue:
      if (a.indirectObjectId !== (b as typeof a).indirectObjectId) {
        return false;
      }
      break;
    case LinkedSelectedOption.Formula:
      if (a.formula !== (b as typeof a).formula) {
        return false;
      }
      break;
  }

  return true;
}

function getDefaultValue(rangeClass: RangeValueBase['rangeClass']) {
  if (
    [EmbeddedIdEnum.RangeClassFailHighEId, EmbeddedIdEnum.RangeClassWarnHighEId].includes(
      rangeClass,
    )
  ) {
    return 99999;
  }
  return 0;
}

export const useRange = ({ inspectionID, data }: CommonHookProps) => {
  const proxy = useSelector<any>((state) => state?.app?.proxy) as Proxy;
  const currentMode = useSelector<any>((state) => state.app?.proxy.mode) as string;

  const [loading, setLoading] = useState(false);
  const [initialValue, setInitialValue] = useState<Range>({});
  const [value, setValue] = useState<Range>({});
  const [rangeObjectID, setRangeObjectID] = useState(0);

  const modified =
    value &&
    initialValue &&
    (!compareRangeValues(value.failHigh, initialValue.failHigh) ||
      !compareRangeValues(value.failLow, initialValue.failLow) ||
      !compareRangeValues(value.warnHigh, initialValue.warnHigh) ||
      !compareRangeValues(value.warnLow, initialValue.warnLow));

  useEffect(() => {
    const getRange = (objectId: number) => {
      return proxy.protobuf.request(
        Protobuf.SvPb.InspectionCmdRequest.create({
          inspectionID,
          getEmbeddedValuesRequest: {
            objectId,
          },
        }),
      );
    };

    if (currentMode !== modes.unknownMode.id) {
      const request = Protobuf.SvPb.InspectionCmdRequest.create({
        inspectionID,
        getObjectIdRequest: {
          info: {
            ownerId: parseInt(data.objectID ?? ''),
            infoStruct: {
              objectType: 0x00200000, //SVObjectTypeEnum.SVRangeObjectType
              subType: 0, //SVObjectSubTypeEnum.SVNotSetSubObjectType
              embeddedID: EmbeddedIdEnum.NoEmbeddedId,
            },
          },
        },
      });
      setLoading(true);
      proxy.protobuf
        .request(request)
        .then((res: any) => {
          setRangeObjectID(res?.getObjectIdResponse?.objectId);
          getRange(rangeObjectID).then(async (res: any) => {
            const embeddedValues: EmbeddedValue[] = res?.getEmbeddedValuesResponse?.list ?? [];

            const getRangeValue = async (
              embeddedIdRangeClass: RangeValueBase['rangeClass'],
            ): Promise<RangeValue | undefined> => {
              const embeddedValue = embeddedValues.find(
                (entry) => entry.embeddedId === embeddedIdRangeClass,
              );

              const embeddedValueObjectID = embeddedValue?.objectId;

              const linkedValue = embeddedValue?.linkedValue;
              if (!linkedValue) {
                return undefined;
              }

              const common = {
                inspectionID,
                rangeClass: embeddedIdRangeClass,
                option: linkedValue.option,
                embeddedValueObjectID,
                equationId: linkedValue.equationId,
              };

              switch (linkedValue.option) {
                case LinkedSelectedOption.DirectValue:
                  return {
                    ...common,
                    value:
                      linkedValue.directValue?.dblVal ??
                      linkedValue.value.dblVal ??
                      linkedValue.defaultValue.dblVal ??
                      0,
                  };
                case LinkedSelectedOption.IndirectValue: {
                  return {
                    ...common,
                    value: linkedValue.value.dblVal ?? linkedValue.defaultValue.dblVal ?? 0,
                    indirectObjectId: (() => {
                      try {
                        return parseInt(
                          bytesToString(linkedValue.indirectIDString ?? new Uint8Array()).replace(
                            /[^\d]/g,
                            '',
                          ),
                        );
                      } catch (e) {
                        logger.error(e instanceof Error ? e.message : String(e));
                      }
                    })(),
                  };
                }
                case LinkedSelectedOption.Formula:
                  return {
                    ...common,
                    value: linkedValue.value.dblVal ?? linkedValue.defaultValue.dblVal ?? 0,
                    formula: bytesToString(linkedValue.formula),
                  };
              }
            };
            const range = {
              failHigh: await getRangeValue(EmbeddedIdEnum.RangeClassFailHighEId),
              failLow: await getRangeValue(EmbeddedIdEnum.RangeClassFailLowEId),
              warnHigh: await getRangeValue(EmbeddedIdEnum.RangeClassWarnHighEId),
              warnLow: await getRangeValue(EmbeddedIdEnum.RangeClassWarnLowEId),
            };

            setInitialValue(range);
            setValue(range);
          });
        })
        .catch(logger.error)
        .finally(() => setLoading(false));
    }
  }, [data.objectID, inspectionID, proxy.protobuf, rangeObjectID, currentMode]);

  const saveChanges = useCallback(async () => {
    if (!modified) {
      return [];
    }

    if (
      value.failHigh?.option === LinkedSelectedOption.DirectValue &&
      value.failLow?.option === LinkedSelectedOption.DirectValue &&
      value.failHigh?.value < value.failLow?.value
    ) {
      return ['Fail low value is bigger than fail high value'];
    }

    if (
      value.warnHigh?.option === LinkedSelectedOption.DirectValue &&
      value.warnLow?.option === LinkedSelectedOption.DirectValue &&
      value.warnHigh?.value < value.warnLow?.value
    ) {
      return ['Warn low value is bigger than warn high value'];
    }
    const req = Protobuf.SvPb.InspectionCmdRequest.create({
      inspectionID,
      setEmbeddedValuesRequest: {
        objectId: rangeObjectID,
        list: Object.values(value).reduce(
          (
            acc: { values: EmbeddedValue; isModified: boolean }[],
            rangeValue: RangeValue | undefined,
          ) => {
            if (!rangeValue) {
              return acc;
            }
            acc.push({
              values: {
                objectId: rangeValue.embeddedValueObjectID ?? 0,
                embeddedId: rangeValue.rangeClass ?? EmbeddedIdEnum.NoEmbeddedId,
                linkedValue: {
                  option: rangeValue.option,
                  defaultValue: {
                    type: VARENUM.VT_R8,
                    dblVal: getDefaultValue(rangeValue.rangeClass),
                  },
                  value: { type: VARENUM.VT_R8, dblVal: clampRangeValue(rangeValue.value ?? 0) },
                  directValue:
                    rangeValue.option === LinkedSelectedOption.DirectValue
                      ? { type: VARENUM.VT_R8, dblVal: clampRangeValue(rangeValue.value ?? 0) }
                      : undefined,
                  indirectIDString:
                    rangeValue.option === LinkedSelectedOption.IndirectValue
                      ? stringToBytes(`{#${rangeValue.indirectObjectId}}`)
                      : undefined,
                  equationId: rangeValue.equationId,
                  formula:
                    rangeValue.option === LinkedSelectedOption.Formula
                      ? stringToBytes(rangeValue.formula?.replace(/\n/g, ''))
                      : undefined,
                },
              },
              isModified: true,
            });
            return acc;
          },
          [],
        ),
        resetType: ResetType.RT_FromObject,
      },
    });
    const res = await proxy.protobuf.request(req);
    const errors: string[] = res?.standardResponse?.errorMessages?.messages?.map(
      (msg: any) => msg.messageText,
    );
    if (!errors?.length) {
      setInitialValue(value);
    }
    return errors ?? [];
  }, [inspectionID, modified, proxy.protobuf, value, rangeObjectID]);

  return { value, setValue, modified, loading, saveChanges };
};
