import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import iconPause from '../../../../assets/icons/pause.svg';
import iconPlay from '../../../../assets/icons/play.svg';
import ActionBar from '../../../../components/ActionBar';
import Container from '../../../../components/layout/Container';
import Product from '../../../../protobuf/Product';
import Proxy from '../../../../protobuf/Proxy';
import { setSaveFlag } from '../../../../redux/actions';
import { TOGGLE_BUTTON } from '../../../../types/ActionBarType';
import {
  drawInspectionOverlays,
  extractInspectionOverlays,
} from '../../../../utils/OverlayRenderer';
import logger from '../../../../utils/logger';
import modes from '../../../../utils/modes';
import FilmStrip, { ImageSchema } from '../FilmStrip';

interface FilmStripsContainerProps {
  id: string;
  inspectionId: string;
}

const FilmStripsContainer = ({ inspectionId, id: viewId }: FilmStripsContainerProps) => {
  const proxy = useSelector<any>((state) => state.app?.proxy) as Proxy;
  const pausedInspections = useSelector<any>((state) => state.app?.pausedInspections) as string[];
  const currentMode = useSelector<any>(
    (state) => state.app?.currentMode,
  ) as typeof modes[keyof typeof modes];
  const isBrowserWindowVisible = useSelector<any>((state) => state.app?.isBrowserWindowVisible) as
    | boolean
    | undefined;
  const sourceImage = useSelector<any>((state) =>
    Number(state.inspections[inspectionId]?.sharedState?.views[viewId]?.sourceImage),
  ) as number;

  const dispatch = useDispatch();

  const canvasRef = useRef<HTMLCanvasElement>(null);

  const [imageResolution, setImageResolution] = useState({ width: 0, height: 0 });
  const [images, setImages] = useState<ImageSchema[]>([]);

  useEffect(() => {
    let productSub: string | null = null;
    const imageStripLength = 10;
    let internalImageResolution = { width: 0, height: 0 };
    let internalImages: ImageSchema[] = [];

    const updateImageDimensions = (product: Product) => {
      if (!product) {
        logger.error(`updateImageDimensions requires product but did not receive.`);
        return;
      }

      if (!product.isReject) {
        logger.error(`updateImageDimensions requires product that is a reject.`);
        return;
      }

      const { image } = product.getImage(sourceImage);

      if (!image) {
        logger.error(`Product does not contain an Image.`);
        return;
      }

      const { height, width } = image;

      if (
        !internalImageResolution.width ||
        !internalImageResolution.height ||
        internalImageResolution.width !== width ||
        internalImageResolution.height !== height
      ) {
        setImageResolution((internalImageResolution = { width, height }));
      }
    };

    const drawImageAndOverlaysToCanvas = ({
      drawTarget,
      product,
    }: {
      drawTarget: HTMLCanvasElement;
      product: Product;
    }) => {
      if (!drawTarget || !product) {
        logger.error(
          'drawImageAndOverlaysToCanvas expects drawTarget and product but did not receive.',
        );
        return;
      }

      const imageId = sourceImage;
      const image = product.getImage(imageId);
      if (!image || !image.domImage) {
        logger.warn(`ImageStrip expected an image onReject but didn't receive`);
        return;
      }

      const drawTargetContext = drawTarget.getContext('2d');
      drawTargetContext?.clearRect(0, 0, drawTarget.width, drawTarget.height);
      drawTargetContext?.drawImage(image.domImage, 0, 0);

      const overlays = extractInspectionOverlays(product, imageId);
      const boundingCoordinates = {
        minX: 0,
        minY: 0,
        maxX: image.width || 0,
        maxY: image.height || 0,
      };

      drawInspectionOverlays({
        drawTarget,
        overlays,
        zoomFactor: 1,
        boundingCoordinates,
        width: image.width,
        height: image.height,
        currentMode,
      });
      return drawTarget.toDataURL('image/bmp');
    };

    const releaseProducts = (products: Product[]) => {
      (products || []).forEach((product) => product.release());
    };

    const onProduct = ({ product }: { product: Product }) => {
      const entry = internalImages.find((e) => e.product === product);
      if (entry || !canvasRef.current) {
        // We can free this reference. the second reference to the
        // same product will be released in this.releaseProducts().
        product.release();
        return;
      }
      let productsToRelease: Product[] = [];

      // TODO: Image dimensions won't change for a subscription
      // better to update `onSubscribed`
      updateImageDimensions(product);

      const combinedSrc = drawImageAndOverlaysToCanvas({
        drawTarget: canvasRef.current,
        product,
      });
      if (combinedSrc) {
        const updatedImages = [{ combinedSrc, product }, ...internalImages];

        // sort descending by trigger count. can happen at beginning of stream.
        updatedImages.sort((a, b) => b.product.trigger - a.product.trigger);

        if (updatedImages.length > imageStripLength) {
          productsToRelease = updatedImages.splice(imageStripLength).map((e) => e.product);
        }

        setImages((internalImages = updatedImages));
      }

      releaseProducts(productsToRelease);
    };

    const subscribeToProductStream = (sourceImage: string | number) => {
      if (!productSub) {
        const desc = {
          inspectionId,
          startAtTriggerCount: -imageStripLength,
          rejectImageIds: [sourceImage],
          allowOldTrigger: true,
          manualRelease: true,
        };
        productSub = proxy.subscribeToProductStream(desc, onProduct);
      }
    };

    const unsubscribeFromProductStream = () => {
      if (productSub) {
        proxy.unsubscribeFromProductStream(productSub);
        productSub = null;
      }

      (internalImages || []).forEach((image) => {
        image.product?.release();
      });
      setImages((internalImages = []));
    };

    if (sourceImage) {
      unsubscribeFromProductStream();
      subscribeToProductStream(sourceImage);
    }

    if (isBrowserWindowVisible !== undefined) {
      if (isBrowserWindowVisible) {
        subscribeToProductStream(sourceImage);
      } else {
        unsubscribeFromProductStream();
      }
    }

    return () => {
      unsubscribeFromProductStream();
    };
  }, [currentMode, inspectionId, isBrowserWindowVisible, proxy, sourceImage]);

  const resumeStream = useCallback(() => {
    proxy.resumeRejectStream(inspectionId);
    dispatch(setSaveFlag(true));
  }, [dispatch, inspectionId, proxy]);

  const pauseStream = useCallback(() => {
    proxy.pauseRejectStream(inspectionId);
    dispatch(setSaveFlag(true));
  }, [dispatch, inspectionId, proxy]);

  const selectProduct = useCallback(
    (product: Product) => {
      pauseStream();
      proxy.replayProduct(inspectionId, product);
      // TODO some highlights?
    },
    [inspectionId, pauseStream, proxy],
  );

  const actions = useMemo(() => {
    const showPause = () => {
      if (inspectionId) {
        if (!pausedInspections.includes(inspectionId)) {
          return true;
        }
      }
      return false;
    };

    return [
      {
        type: TOGGLE_BUTTON,
        id: inspectionId,
        intlId: 'imageStripView.play',
        iconUrl: iconPlay,
        enabled: true,
        onClickHandler: resumeStream,
        intlIdAlternative: 'imageStripView.pause',
        iconUrlAlternative: iconPause,
        onClickHandlerAlternative: pauseStream,
        showAlternative: showPause(),
      },
    ];
  }, [inspectionId, pauseStream, pausedInspections, resumeStream]);

  return (
    <div className="reject-view-container">
      <Container>
        <canvas
          ref={canvasRef}
          style={{ display: 'none' }}
          width={imageResolution.width}
          height={imageResolution.height}
          className="imageCanvas"
        />
        {images && images.length > 0 && <FilmStrip onImageClick={selectProduct} images={images} />}
        <ActionBar isViewAction actions={actions} />
      </Container>
    </div>
  );
};

export default FilmStripsContainer;
