import { useCallback, useMemo, useRef, useState } from 'react';
import classNames from 'classnames';
import { FormattedMessage } from 'react-intl';
import {
  Mosaic,
  MosaicBranch,
  MosaicContext,
  MosaicNode,
  MosaicPath,
} from 'react-mosaic-component';
import { useDispatch, useSelector } from 'react-redux';
import ConfirmDialog from '../../components/ConfirmDialog';
import {
  increaseViewSizeChangeCounter,
  purgeViewState,
  clearCurrentView,
} from '../../redux/actions/index';
import { Inspections } from '../../types/InspectionsType';
import { canEditViewSet } from '../../utils/permissions';
import SVOView from './SVOView';

import './style.scss';

interface SVOViewSchema {
  viewId: string | number;
  path: MosaicBranch[];
  remove: (path: MosaicPath) => void;
}

interface ViewSchema {
  id: string;
  name: string;
  initialValue: MosaicNode<string | number> | null;
  viewSet: Record<
    string,
    {
      id: string;
      inspectionId: number;
      titleId: string;
      viewComponentClassId: string;
    }
  >;
}

interface ViewSetProps {
  views: ViewSchema;
  onChange: (layout: MosaicNode<string | number> | null) => void;
  topNavHidden?: boolean;
  bottomNavHidden?: boolean;
}

const ViewSet = ({
  views,
  onChange,
  topNavHidden = false,
  bottomNavHidden = false,
}: ViewSetProps) => {
  const inspections = useSelector<any>((state) => state.inspections) as Inspections;
  const myPermissions = useSelector<any>((state) => state.app?.myPermissions) as Set<string>;

  const dispatch = useDispatch();

  const mosaic = useRef(null);

  const [viewToClose, setViewToClose] = useState<SVOViewSchema | null>(null);

  const editingViewIds = useMemo(
    () =>
      Object.keys(inspections).map(
        (inspection) => inspections[inspection].sharedState?.editingViewId,
      ),
    [inspections],
  );

  const viewSetClass = classNames({
    'view-set-wrapper': true,
    'bottom-nav-offset': !bottomNavHidden,
    'top-nav-hidden': !topNavHidden,
  });

  const handleConfirmDialogClose = useCallback(
    (confirmed: boolean) => {
      if (confirmed && viewToClose) {
        const { viewId, path, remove } = viewToClose;
        const inspectionId = views.viewSet[viewId] && views.viewSet[viewId].inspectionId;
        if (typeof views.initialValue === 'string') {
          dispatch(clearCurrentView());
        }
        dispatch(purgeViewState(inspectionId, viewId));

        remove(path);
      }
      setViewToClose(null);
    },
    [dispatch, viewToClose, views.initialValue, views.viewSet],
  );

  return (
    <div className={viewSetClass}>
      <ConfirmDialog
        open={!!viewToClose}
        onClose={handleConfirmDialogClose}
        message={<FormattedMessage id="svoView.closeViewConfirmation" />}
      />
      <Mosaic
        onChange={(layout: MosaicNode<string | number> | null) => {
          onChange(layout);
          dispatch(increaseViewSizeChangeCounter());
        }}
        ref={mosaic}
        renderTile={(id, path) => (
          <MosaicContext.Consumer>
            {({ mosaicActions }) => (
              <SVOView
                key={id}
                id={id}
                path={path}
                views={views}
                dispatch={dispatch}
                editingViewIds={editingViewIds}
                onCloseView={() => {
                  setViewToClose({ viewId: id, path, remove: mosaicActions.remove });
                }}
              />
            )}
          </MosaicContext.Consumer>
        )}
        initialValue={views.initialValue}
        resize={canEditViewSet(myPermissions) ? { minimumPaneSizePercentage: 10 } : 'DISABLED'}
      />
    </div>
  );
};

export default ViewSet;
