import { memo, useCallback, useEffect, useMemo, useState } from 'react';
import { AutoResizer } from 'react-base-table';
import { useIntl } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import EditIcon from '../../../../assets/icons/edit.svg';
import DraggableTable from '../../../../components/DraggableTable';
import IconButton from '../../../../components/buttons/IconButton';
import Configuration from '../../../../protobuf/Configuration';
import Product from '../../../../protobuf/Product';
import Proxy from '../../../../protobuf/Proxy';
import { setSaveFlag, setViewData } from '../../../../redux/actions';
import { canEditListView } from '../../../../utils/permissions';
import AliasEditor, { Alias } from './AliasEditor';

interface ColumnType {
  key: string;
  dataKey: string;
  title: string;
  width: number;
  flexGrow?: number;
}

interface ListProps {
  viewId: string;
  inspectionId: string;
}

export const List = memo(({ viewId, inspectionId }: ListProps) => {
  const proxy = useSelector<any>((state) => state.app?.proxy) as Proxy;
  const configuration = useSelector<any>((state) => state.app?.configuration) as Configuration;
  const myPermissions = useSelector<any>((state) => state.app?.myPermissions) as Set<string>;
  const isSelectionMode = useSelector<any>(
    (state) => state.inspections[inspectionId]?.views[viewId]?.isSelectionMode ?? true,
  ) as boolean;
  const showAliases = useSelector<any>(
    (state) => state.inspections[inspectionId]?.sharedState?.showAliasesInList,
  ) as boolean;
  const locale = useSelector<any>((state) => state.app.locale) as string;
  const aliases = useSelector<any>(
    (state) => state.inspections[inspectionId]?.sharedState?.listViewAliases ?? new Map(),
  ) as Map<string | number, Alias>;
  const selection = useSelector<any>(
    (state) => state.inspections[inspectionId]?.views[viewId]?.selection ?? [],
  ) as React.Key[];
  const dispatch = useDispatch();
  const intl = useIntl();

  const [listValues, setListValues] = useState<Alias[]>([]);
  const [isAliasEditorOpen, setIsAliasEditorOpen] = useState(false);
  const [selectedAlias, setSelectedAlias] = useState<Alias | null>(null);

  useEffect(() => {
    let productSub: string | null = null;

    const createListValues = (product: Product): Alias[] => {
      const values: Omit<Alias, 'name'>[] = product.getValuesAsString(selection);
      return values.map((entry: { id: string; value: string }) => ({
        ...entry,
        name: configuration.getValueNameForId(inspectionId, entry.id) || '',
      }));
    };

    const onProduct = ({ product }: { product: Product }) => {
      const listValues = createListValues(product);
      setListValues(listValues);
      return Promise.resolve();
    };

    const subscribeToProductStream = () => {
      if (!productSub) {
        const startAtTriggerCount = -1; // list view can only show one item at a time
        const desc = { inspectionId, valueIds: selection, startAtTriggerCount };
        productSub = proxy.subscribeToProductStream(desc, onProduct);
      }
    };

    const unsubscribeFromProductStream = () => {
      if (productSub) {
        proxy.unsubscribeFromProductStream(productSub);
        productSub = null;
      }
    };

    unsubscribeFromProductStream();
    if (!isSelectionMode && selection.length > 0) {
      subscribeToProductStream();
    }

    return () => {
      unsubscribeFromProductStream();
    };
  }, [configuration, inspectionId, isSelectionMode, proxy, selection]);

  const columns = useMemo(() => {
    const columns: ColumnType[] = [
      {
        key: showAliases ? 'Alias/Name' : 'Name',
        dataKey: 'name',
        title: showAliases ? intl.messages['listView.aliasName'] : intl.messages['listView.name'],
        width: 0,
        flexGrow: 2,
      },
      {
        key: 'Values',
        dataKey: 'value',
        title: intl.messages['listView.values'],
        width: 0,
        flexGrow: 1,
      },
    ];

    if (canEditListView(myPermissions)) {
      columns.push({
        key: '',
        dataKey: 'editAlias',
        title: '',
        width: 50,
      });
    }
    return columns;
  }, [intl.messages, myPermissions, showAliases]);

  const tableData = useMemo(() => {
    return listValues.map((row) => {
      const alias = aliases.get(row.id)?.alias;

      return {
        ...row,
        name: showAliases ? alias?.[locale] || row.name : row.name,
        editAlias: (
          <IconButton
            tooltip={intl.messages['listView.AliasEditor.title']}
            onClick={() => {
              setSelectedAlias(row);
              setIsAliasEditorOpen(true);
            }}
            iconUrl={EditIcon}
            key="editAlias"
          />
        ),
      };
    });

    //isAliasEditorOpen is included in dependency array to force alias updates after editor closes (it is due to use of immutable Map of aliases in reducer)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [aliases, intl.messages, listValues, locale, showAliases, isAliasEditorOpen]);

  const currentValueObject = useMemo(
    () => (listValues || []).find((value) => value.id === selectedAlias?.id),
    [listValues, selectedAlias?.id],
  );

  const saveNewOrder = useCallback(
    (before: number, after: number) => {
      const [removed] = selection.splice(before, 1);
      selection.splice(after, 0, removed);
      dispatch(setViewData(inspectionId, viewId, 'selection', selection));
      dispatch(setSaveFlag(true));
    },
    [dispatch, inspectionId, selection, viewId],
  );

  const closeAliasEditor = useCallback(
    (alias: Alias, isSaved: boolean) => {
      if (alias) {
        if (!alias.alias) {
          alias.alias = {};
        }
        Object.keys(alias.alias).forEach((language) => {
          if (alias.alias?.[language] === '') {
            delete alias.alias[language];
          }
        });

        const isEmpty = Object.keys(alias.alias).length === 0;
        if (aliases.has(alias.id)) {
          const newAlias = aliases.get(alias.id) as Alias;
          newAlias.alias = alias.alias;

          if (isEmpty) {
            aliases.delete(alias.id);
          } else {
            aliases.set(alias.id, newAlias);
          }
        } else {
          if (!isEmpty) {
            aliases.set(alias.id, {
              ...alias,
              objectId: alias.id,
              dottedName: alias.name,
              alias: alias.alias,
            });
          }
        }
      }

      setIsAliasEditorOpen(false);
      if (isSaved) {
        dispatch(setSaveFlag(true));
      }
    },
    [aliases, dispatch],
  );

  return (
    <div style={{ height: '100%' }}>
      <AutoResizer>
        {({ width, height }) => (
          <DraggableTable
            columns={columns}
            data={tableData}
            width={width}
            height={height}
            headerHeight={30}
            rowHeight={30}
            onSequenceChange={saveNewOrder}
          />
        )}
      </AutoResizer>
      {currentValueObject && isAliasEditorOpen && (
        <AliasEditor
          onClose={(alias, isSaved = false) => closeAliasEditor(alias, isSaved)}
          open={isAliasEditorOpen}
          selectedAlias={currentValueObject}
        />
      )}
    </div>
  );
});
