import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import useCancellablePromise from '../../../../hooks/useCancellablePromise';
import Protobuf from '../../../../protobuf/Protobuf';
import type Proxy from '../../../../protobuf/Proxy';
import { bytesToString } from '../../common';

export const useIndirectValueObjectName = (inspectionID: number, objectId?: number) => {
  const proxy = useSelector<any>((state) => state?.app?.proxy) as Proxy;
  const cancellable = useCancellablePromise();

  const [name, setName] = useState('');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!objectId) {
      return;
    }
    setLoading(true);
    cancellable(
      proxy.protobuf.request(
        Protobuf.SvPb.InspectionCmdRequest.create({
          inspectionID,
          getObjectNameRequest: {
            BeforeType: 0x00001102, //SVObjectTypeEnum.BlobDrawObjectType
            objectId,
          },
        }),
      ),
    )
      .then((res: any) => {
        setName(
          bytesToString(res?.getObjectNameResponse?.name) || res?.getObjectNameResponse?.name,
        );
        setLoading(false);
      })
      .catch((error: any) => error && setLoading(false));
  }, [cancellable, inspectionID, objectId, proxy.protobuf]);

  return { name, loading };
};
