import React, { useCallback, useEffect, useRef, useState } from 'react';
import Table, { BaseTableProps, CallOrReturn, callOrReturn, ColumnShape } from 'react-base-table';
import {
  ContainerGetter,
  HelperContainerGetter,
  SortableContainer,
  SortableElement,
  SortableHandle,
  SortEndHandler,
} from 'react-sortable-hoc';

import DragIcon from '../../assets/icons/drag.svg';

import './style.css';

type SortableArg = { children: React.ReactElement<any, any> | null };
const DraggableContainer = SortableContainer(({ children }: SortableArg) => children);
const DraggableElement = SortableElement(({ children }: SortableArg) => children);
const DraggableHandle = SortableHandle(({ children }: SortableArg) => children);

interface RowProps
  extends React.DetailedHTMLProps<React.HTMLAttributes<HTMLDivElement>, HTMLDivElement> {
  key: React.Key;
  index: number;
  draggable: boolean;
}

const Row: React.FC<React.PropsWithChildren<RowProps>> = ({
  key,
  index,
  children,
  draggable,
  ...rest
}) => {
  if (!draggable) {
    return <div {...rest}>{children}</div>;
  }

  return (
    <DraggableElement key={key} index={index}>
      <div {...rest}>
        <DraggableHandle>
          <div className="draggable-table-handle">
            <img src={DragIcon} alt="Drag" />
          </div>
        </DraggableHandle>
        {children}
      </div>
    </DraggableElement>
  );
};

type RowPropsHandler<T = unknown> = CallOrReturn<
  Record<string, any>,
  {
    columns: ColumnShape<T>[];
    rowData: T;
    rowIndex: number;
  }
>;

interface DraggableTableProps<T = unknown> extends BaseTableProps<T> {
  draggable?: boolean;
  data: T[];
  onSequenceChange: (oldIndex: number, newIndex: number) => void;
  rowProps?: Record<string, any>;
}

const DraggableTable = React.memo(
  <T extends Record<string, any> = Record<string, any>>({
    draggable = true,
    data: initialData,
    onSequenceChange,
    rowProps,
    ...tableProps
  }: DraggableTableProps<T>) => {
    const tableRef = useRef<Table<T>>(null);

    const [data, setData] = useState<typeof initialData>([]);

    useEffect(() => {
      setData(initialData);
    }, [initialData]);

    const getContainer: ContainerGetter = () => {
      return tableRef.current?.getDOMNode()?.querySelector('.BaseTable__body') as HTMLElement;
    };

    const getHelperContainer: HelperContainerGetter = () => {
      return tableRef.current?.getDOMNode()?.querySelector('.BaseTable__table') as HTMLElement;
    };

    const rowPropsHandler: RowPropsHandler<T> = (args) => {
      const extraProps = callOrReturn(rowProps);
      return {
        ...extraProps,
        draggable,
        tagName: Row,
        index: args.rowIndex,
      };
    };

    const handleSortEnd: SortEndHandler = useCallback(
      ({ oldIndex, newIndex }) => {
        onSequenceChange(oldIndex, newIndex);
        const newData = [...data];
        const [removed] = newData.splice(oldIndex, 1);
        newData.splice(newIndex, 0, removed);
        setData(newData);
      },
      [data, onSequenceChange],
    );

    if (!draggable) {
      return <Table {...tableProps} ref={tableRef} data={data} rowProps={rowPropsHandler} />;
    }

    return (
      <DraggableContainer
        useDragHandle
        getContainer={getContainer}
        helperContainer={getHelperContainer}
        onSortEnd={handleSortEnd}
      >
        <Table
          {...tableProps}
          ref={tableRef}
          data={data}
          rowProps={rowPropsHandler}
          className="draggable"
        />
      </DraggableContainer>
    );
  },
);

export default DraggableTable;
