import { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Configuration from '../../protobuf/Configuration';
import { initViewState, setViewData } from '../../redux/actions';
import { canReadImageStripView, canEditImageStripView } from '../../utils/permissions';
import FilmStripsContainer from './components/FilmStripsContainer';
import ObjectSelector from './components/ObjectSelector';
import './styles.scss';

interface ImageStripViewProps {
  id: string;
  inspectionId: string;
}

const ImageStripView = ({ id: viewId, inspectionId }: ImageStripViewProps) => {
  const configuration = useSelector<any>(
    (state) => state.app?.configuration ?? null,
  ) as Configuration;
  const myPermissions = useSelector<any>((state) => state.app?.myPermissions) as Set<string>;
  const sourceImage = useSelector<any>((state) =>
    Number(state.inspections[inspectionId]?.sharedState?.views[viewId]?.sourceImage),
  ) as number;

  const dispatch = useDispatch();

  useEffect(() => {
    const initState = {};
    const initSharedState = {
      sourceImage: 0,
    };
    dispatch(initViewState(inspectionId, viewId, initState, initSharedState));

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [inspectionId, viewId]);

  const setTitleFromConfig = useCallback(() => {
    if (configuration) {
      const path = configuration.getValueNameForId(inspectionId, sourceImage);
      if (path) {
        dispatch(setViewData(inspectionId, viewId, 'windowName', path));
      }
    }
  }, [configuration, dispatch, inspectionId, sourceImage, viewId]);

  setTitleFromConfig();
  const canEdit = canEditImageStripView(myPermissions);
  const canRead = canReadImageStripView(myPermissions);

  if (canRead || canEdit) {
    return (
      <div className="reject-view-container">
        {!sourceImage && canEdit ? (
          <ObjectSelector inspectionId={inspectionId} id={viewId} />
        ) : (
          <FilmStripsContainer inspectionId={inspectionId} id={viewId} />
        )}
      </div>
    );
  }

  return <div></div>;
};

export default ImageStripView;
