import Product from '../../../../protobuf/Product';
import './styles.scss';

export interface ImageSchema {
  combinedSrc: string;
  product: Product;
}

interface FilmStripProps {
  images: ImageSchema[];
  onImageClick: (product: Product) => void;
}

const FilmStrip = ({ images, onImageClick }: FilmStripProps) => {
  return (
    <div className="film-strip-container">
      {images.map((image) => (
        <button
          key={image.product.trigger}
          className="reject-image"
          onClick={() => {
            onImageClick(image.product);
          }}
        >
          <img src={image.combinedSrc} alt="" />
        </button>
      ))}
    </div>
  );
};

export default FilmStrip;
