import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import Protobuf from '../../../../protobuf/Protobuf';
import type Proxy from '../../../../protobuf/Proxy';
import logger from '../../../../utils/logger';
import modes from '../../../../utils/modes';
import { CommonHookProps, EmbeddedIdEnum } from '../../common';

export interface EquationValue {
  equationText: string;
  equationObjectID: number;
}

export const useFormula = ({
  inspectionID,
  data,
  conditional,
}: CommonHookProps & { conditional?: boolean }) => {
  const proxy = useSelector<any>((state) => state?.app?.proxy) as Proxy;
  const currentMode = useSelector<any>((state) => state.app?.proxy.mode) as string;

  const [loading, setLoading] = useState(false);
  const [initialValue, setInitialValue] = useState<EquationValue | null>(null);
  const [value, setValue] = useState<EquationValue | null>(null);

  const modified = value !== initialValue;

  useEffect(() => {
    const getEquation = (objectId: number) => {
      return proxy.protobuf.request(
        Protobuf.SvPb.InspectionCmdRequest.create({
          inspectionID,
          getEquationRequest: {
            objectId,
          },
        }),
      );
    };

    if (currentMode !== modes.unknownMode.id) {
      const request = Protobuf.SvPb.InspectionCmdRequest.create({
        inspectionID,
        getObjectIdRequest: {
          info: {
            ownerId: parseInt(data.objectID ?? ''),
            infoStruct: {
              objectType: 8, //SVObjectTypeEnum.SVEquationObjectType
              subType: conditional ? 1024 : 1025, //conditional ? SVObjectSubTypeEnum.SVConditionalObjectType : SVObjectSubTypeEnum.SVMathEquationObjectType
              embeddedID: EmbeddedIdEnum.NoEmbeddedId,
            },
          },
        },
      });
      setLoading(true);
      proxy.protobuf
        .request(request)
        .then((res: any) => {
          const equationObjectID: number = res?.getObjectIdResponse?.objectId;
          getEquation(equationObjectID).then((res: any) => {
            const equationValue: EquationValue = {
              equationText: res?.getEquationResponse?.equationText ?? '',
              equationObjectID,
            };
            setValue(equationValue);
            setInitialValue(equationValue);
          });
        })
        .catch(logger.error)
        .finally(() => setLoading(false));
    }
  }, [conditional, data.objectID, inspectionID, proxy.protobuf, currentMode]);

  const saveChanges = useCallback(async () => {
    if (!modified || !value) {
      return [];
    }

    const res = await proxy.protobuf.request(
      Protobuf.SvPb.InspectionCmdRequest.create({
        inspectionID,
        validateAndSetEquationRequest: {
          objectId: value.equationObjectID,
          equationText: value.equationText.replace(/([^\r])\n/g, '$1\r\n'),
          bSetValue: true,
        },
      }),
    );

    const errors: string[] = res?.standardResponse?.errorMessages?.messages?.map(
      (msg: any) => msg.messageText,
    );
    if (!errors?.length) {
      setInitialValue(value);
    }
    return errors ?? [];
  }, [inspectionID, modified, proxy.protobuf, value]);

  return { value, setValue, modified, loading, saveChanges };
};
