import { memo, useEffect, useMemo, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import ObjectSelector, { TreeNodeSchema } from '../../../../components/ObjectSelector';
import Configuration from '../../../../protobuf/Configuration';
import { setViewData, setSaveFlag } from '../../../../redux/actions';

interface ValueSelectorProps {
  viewId: string;
  inspectionId: string;
}

const ValueSelector = memo(({ viewId, inspectionId }: ValueSelectorProps) => {
  const configuration = useSelector<any>((state) => state.app?.configuration) as Configuration;
  const selection = useSelector<any>(
    (state) => state.inspections[inspectionId]?.views[viewId]?.selection ?? [],
  ) as React.Key[];
  const dispatch = useDispatch();

  const [expandedKeys, setExpandedKeys] = useState<React.Key[]>([]);

  const { tree, filterIgnore } = useMemo(() => {
    if (!configuration || !inspectionId) {
      return { tree: null, filterIgnore: [] };
    }
    const name = configuration.getInspectionName(inspectionId);
    const filterIgnore = [`Inspections.${name}`, `Inspections${name}`, name];

    const items = configuration.getObjectSelectorItemsForInspection(inspectionId);
    if (!items) {
      return { tree: null, filterIgnore };
    }
    return {
      tree: configuration.filterArrayValuesFromTree(items.tree) as TreeNodeSchema,
      filterIgnore,
    };
  }, [configuration, inspectionId]);

  useEffect(() => {
    if (tree?.children?.length === 1) {
      setExpandedKeys((keys) => (tree.children[0].name ? [...keys, tree.children[0].name] : keys));
    }
  }, [tree]);

  if (!tree) {
    return <div style={{ width: '100%', overflow: 'auto', height: '100%' }}></div>;
  }

  return (
    <div style={{ width: '100%', overflow: 'auto', height: '100%' }}>
      <ObjectSelector
        data={tree}
        onChange={(selectedIds) => {
          dispatch(setViewData(inspectionId, viewId, 'selection', selectedIds));
          dispatch(setSaveFlag(true));
        }}
        onExpand={setExpandedKeys}
        selection={selection}
        expandedKeys={expandedKeys}
        filterIgnore={filterIgnore}
      />
    </div>
  );
});

export default ValueSelector;
