import { ReactElement } from 'react';
import { TextField, InputAdornment, IconButton } from '@material-ui/core';
import { MuiThemeProvider, createTheme, makeStyles } from '@material-ui/core/styles';
import { FormattedMessage } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import ClearIcon from '../../../../assets/icons/clear.svg';
import { OutlinedSelection } from '../../../../components/OutlinedSelection';
import Configuration from '../../../../protobuf/Configuration';
import { setSaveFlag } from '../../../../redux/actions';
import { canEditParameterView } from '../../../../utils/permissions';
import { OverlaySchema } from '../../../../utils/reduxHelper';

const useStyles = makeStyles(() => ({
  header: {
    fontSize: '1.8rem',
    fontWeight: 'bold',
    marginBottom: 30,
  },
  input: {
    height: 37,
    fontSize: '1.4rem',
  },
  label: {
    fontSize: '1.4rem',
    transform: 'translate(14px, 11px) scale(1)',
  },
}));

const theme = createTheme({
  typography: {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    //@ts-ignore
    useNextVariants: true,
    htmlFontSize: 10,
  },
});

interface OverlayPropertyListProps {
  inspectionId: string;
  viewId: string;
  currentOverlay: OverlaySchema;
  imageDimensions?: { width: number; height: number };
  onChange: (changedValue: Partial<OverlaySchema>) => void;
  openSelection: (variableName: any, variableId: keyof OverlaySchema) => void;
}

export const OverlayPropertyList = ({
  inspectionId,
  currentOverlay,
  imageDimensions,
  onChange,
  openSelection,
}: OverlayPropertyListProps) => {
  const configuration = useSelector<any>((state) => state.app?.configuration) as Configuration;
  const myPermissions = useSelector<any>((state) => state.app?.myPermissions) as Set<string>;
  const dispatch = useDispatch();

  const classes = useStyles();

  const imageWidth = imageDimensions?.width ?? 0;
  const imageHeight = imageDimensions?.height ?? 0;

  const minWidth = Number(-imageWidth * 2);
  const maxWidth = Number(imageWidth * 2);

  const minHeight = Number(-imageHeight * 2);
  const maxHeight = Number(imageHeight * 2);

  const canEdit = canEditParameterView(myPermissions);

  const handleChange = (changedValue: Partial<OverlaySchema>) => {
    dispatch(setSaveFlag(true));
    onChange(changedValue);
  };

  type CommonFieldProps = {
    name: ReactElement;
    id: keyof OverlaySchema;
    disabled: boolean;
  };

  const renderTextField = ({ name, id, disabled }: CommonFieldProps) => {
    return (
      <TextField
        label={name}
        style={{ width: '100%' }}
        id={id}
        type="text"
        variant="outlined"
        disabled={disabled || !currentOverlay}
        InputProps={{ classes: { input: classes.input } }}
        InputLabelProps={{ classes: { root: classes.label } }}
        value={(currentOverlay && currentOverlay[id]) || ''}
        onChange={(e) => handleChange({ [id]: e.target.value })}
      />
    );
  };

  const renderNumberField = ({
    name,
    id,
    min,
    max,
    disabled,
  }: CommonFieldProps & { min: number; max: number }) => {
    const value = (currentOverlay && currentOverlay[id]) || '';
    const trimToMinMax = (min: number, max: number, value: string) => {
      let valueNumber = Number(value);
      if (valueNumber > max) {
        valueNumber = max;
      }
      if (valueNumber < min) {
        valueNumber = min;
      }

      return valueNumber;
    };
    return (
      <TextField
        label={name}
        style={{ width: '100%' }}
        id={id}
        type="number"
        variant="outlined"
        disabled={disabled || !currentOverlay}
        InputProps={{
          classes: { input: classes.input },
          inputProps: { min, max },
        }}
        InputLabelProps={{ classes: { root: classes.label } }}
        value={value}
        onChange={(e) => handleChange({ [id]: trimToMinMax(min, max, e.target.value) })}
      />
    );
  };

  const renderSelection = ({
    name,
    id,
    options,
    disabled,
  }: CommonFieldProps & { options: string[] }) => {
    return (
      <OutlinedSelection
        selectedValue={currentOverlay && currentOverlay[id]}
        values={options}
        onSelectionChanged={handleChange}
        name={name}
        id={id}
        disabled={disabled || !currentOverlay}
      />
    );
  };

  const renderObjectSelector = ({ name, id, disabled }: CommonFieldProps) => {
    const value =
      (currentOverlay &&
        currentOverlay[id] &&
        configuration.getValueNameForId(inspectionId, currentOverlay[id])) ||
      '';

    return (
      <TextField
        label={name}
        style={{ width: '100%' }}
        id={id}
        type="text"
        variant="outlined"
        disabled={disabled || !currentOverlay}
        InputProps={{
          classes: { input: classes.input },
          endAdornment: (
            <InputAdornment position="end">
              <IconButton
                edge="end"
                aria-label="clear"
                onClick={() => handleChange({ [id]: '' })}
                title={id}
              >
                <img src={ClearIcon} alt="x" />
              </IconButton>
            </InputAdornment>
          ),
        }}
        InputLabelProps={{ classes: { root: classes.label } }}
        value={value}
        onFocus={() => openSelection(name, id)}
      />
    );
  };

  return (
    <div className="parameter-view-overlay-overlaypropertylist">
      <MuiThemeProvider theme={theme}>
        <label className={classes.header}>
          <FormattedMessage id="parameterview.overlay.parameters" />
        </label>
        {renderTextField({
          name: <FormattedMessage id="parameterview.overlay.label" />,
          id: 'label',
          disabled: !canEdit,
        })}

        {renderNumberField({
          name: <FormattedMessage id="parameterview.overlay.x" />,
          id: 'x',
          min: minWidth,
          max: maxWidth,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: <FormattedMessage id="parameterview.overlay.xVariable" />,
          id: 'xVariable',
          disabled: !canEdit,
        })}

        {renderNumberField({
          name: <FormattedMessage id="parameterview.overlay.offsetLeft" />,
          id: 'offsetLeft',
          min: minWidth,
          max: maxWidth,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: <FormattedMessage id="parameterview.overlay.offsetLeftVariable" />,
          id: 'offsetLeftVariable',
          disabled: !canEdit,
        })}

        {renderNumberField({
          name: <FormattedMessage id="parameterview.overlay.y" />,
          id: 'y',
          min: minHeight,
          max: maxHeight,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: <FormattedMessage id="parameterview.overlay.yVariable" />,
          id: 'yVariable',
          disabled: !canEdit,
        })}

        {renderNumberField({
          name: <FormattedMessage id="parameterview.overlay.offsetTop" />,
          id: 'offsetTop',
          min: minHeight,
          max: maxHeight,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: <FormattedMessage id="parameterview.overlay.offsetTopVariable" />,
          id: 'offsetTopVariable',
          disabled: !canEdit,
        })}

        {renderNumberField({
          name: <FormattedMessage id="parameterview.overlay.width" />,
          id: 'width',
          min: minWidth,
          max: maxWidth,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: <FormattedMessage id="parameterview.overlay.widthVariable" />,
          id: 'widthVariable',
          disabled: !canEdit,
        })}

        {renderNumberField({
          name: <FormattedMessage id="parameterview.overlay.height" />,
          id: 'height',
          min: minHeight,
          max: maxHeight,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: <FormattedMessage id="parameterview.overlay.heightVariable" />,
          id: 'heightVariable',
          disabled: !canEdit,
        })}
        {renderTextField({
          name: <FormattedMessage id="parameterview.overlay.color" />,
          id: 'color',
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: <FormattedMessage id="parameterview.overlay.colorVariable" />,
          id: 'colorVariable',
          disabled: !canEdit,
        })}

        {renderSelection({
          name: <FormattedMessage id="parameterview.overlay.lineStyle" />,
          id: 'lineStyle',
          options: ['solid', 'dotted', 'dashed'],
          disabled: !canEdit,
        })}
      </MuiThemeProvider>
    </div>
  );
};
