import { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ObjectSelectorBase from '../../../../components/ObjectSelector';
import Configuration from '../../../../protobuf/Configuration';
import { setSaveFlag, setSharedStateData, setViewData } from '../../../../redux/actions';
import logger from '../../../../utils/logger';

interface ObjectSelectorProps {
  id: string;
  inspectionId: string;
}

const ObjectSelector = ({ inspectionId, id: viewId }: ObjectSelectorProps) => {
  const configuration = useSelector<any>(
    (state) => state.app?.configuration ?? null,
  ) as Configuration;
  const dispatch = useDispatch();

  const handleObjectSelectorChange = useCallback(
    (selectedId: React.Key | React.Key[], checkedLocation?: string) => {
      const setSourceImageIndex = (id: string) => {
        if (!id) {
          logger.error(`ObjectSelector did not return a valid id.`);
          return;
        }
        dispatch(setSharedStateData(inspectionId, 'sourceImage', id, viewId));
      };

      setSourceImageIndex(selectedId as string);
      dispatch(setViewData(inspectionId, viewId, 'windowName', checkedLocation));
      dispatch(setSaveFlag(true));
    },
    [dispatch, inspectionId, viewId],
  );

  if (!configuration || !inspectionId) {
    return null;
  }

  const images = configuration.getObjectSelectorImagesForInspection(inspectionId);

  return (
    <div style={{ width: '100%', overflow: 'auto' }}>
      {images && (
        <ObjectSelectorBase singleSelect data={images.tree} onChange={handleObjectSelectorChange} />
      )}
    </div>
  );
};

export default ObjectSelector;
