import { useCallback, useEffect, useState } from 'react';
import { Button, CircularProgress } from '@material-ui/core';
import clsx from 'clsx';
import { useIntl } from 'react-intl';
import { useSelector } from 'react-redux';
import { Treebeard, TreeNode } from 'react-treebeard-ts';
import { ReactComponent as ChevronDoubleLeftIcon } from '../../../assets/icons/chevron-double-left.svg';
import Dialog from '../../../components/Dialog';
import Configuration from '../../../protobuf/Configuration';
import TreebeardStyle from '../components/TreebeardStyle';
import { useFormulaValidate } from './hooks/useFormulaValidate';
import { useIndirectValueObjectName } from './hooks/useIndirectValueObjectName';

import './LinkedValueSelectDialog.scss';

interface TreeNodeWithLocation extends TreeNode {
  location: string;
  children?: TreeNodeWithLocation[];
}
interface LinkedValueTreeNode extends TreeNodeWithLocation {
  values: LinkedValueTreeNode[];
  children?: LinkedValueTreeNode[];
  link?: string;
  location: string;
}

interface LinkedValueSelectDialogProps {
  inspectionID: number;
  equationId?: number;
  value?: number;
  onClose: () => void;
  onSelect: (indirectObjectId: number, link: string) => void;
}

export const LinkedValueSelectDialog = ({
  inspectionID,
  equationId = 0,
  value,
  onClose,
  onSelect,
}: LinkedValueSelectDialogProps) => {
  const configuration = useSelector<any>((state) => state?.app?.configuration) as Configuration;
  const intl = useIntl();

  const { name: linkName, loading } = useIndirectValueObjectName(inspectionID, value);
  const formulaValidation = useFormulaValidate(inspectionID, equationId);

  const [treeNodes, setTreeNodes] = useState<LinkedValueTreeNode[]>([]);
  const [selectedNode, setSelectedNode] = useState<LinkedValueTreeNode | null>(null);
  const [selectedValueNode, setSelectedValueNode] = useState<
    LinkedValueTreeNode['values'][number] | null
  >(null);

  const link = selectedValueNode?.link ?? selectedNode?.link;
  const validationError = formulaValidation.validationErrorMessages?.length > 0;

  useEffect(() => {
    const items = configuration.getObjectSelectorItemsForInspection(inspectionID);

    const parseNodes = (
      nodes: TreeNodeWithLocation[] | undefined,
      link = '',
    ): LinkedValueTreeNode[] | undefined => {
      if (!nodes?.length) {
        return undefined;
      }

      return nodes?.map((node) => {
        const leafLink = [link, node.name].filter(Boolean).join('.');
        return {
          ...node,
          toggled: node.name === 'Tool Set' ? true : node.active,
          link: leafLink,
          children: parseNodes(
            node.children?.filter((child) => child.children?.length),
            leafLink,
          ),
          values:
            parseNodes(
              node.children?.filter((child) => !child.children?.length),
              leafLink,
            ) ?? [],
        };
      });
    };

    if (items?.tree?.children) {
      setTreeNodes(parseNodes(items.tree.children) ?? []);
    }
  }, [configuration, inspectionID]);

  useEffect(() => {
    if (loading) {
      return;
    }
    const path = linkName.split('.');
    path.shift(); //removes inspection name
    path.pop(); //removes value name

    const traverse = (
      nodes: LinkedValueTreeNode[] | undefined,
      path: string[],
    ): LinkedValueTreeNode[] | undefined => {
      if (!path.length) {
        return nodes;
      }
      return nodes?.map((node) => {
        const itemName = path[0];
        const found = node.name === itemName;

        if (found && path.length === 1) {
          setSelectedNode(node);
        }

        return {
          ...node,
          children: found ? traverse(node.children, path.slice(1)) : node.children,
          toggled: found ? true : node.toggled,
          active: found && path.length === 1,
          name: (found ? (
            <span className="selected-path-node">{node.name}</span>
          ) : (
            node.name
          )) as never,
        };
      });
    };

    setTreeNodes((nodes) => traverse(nodes, path) ?? []);
  }, [linkName, loading]);

  useEffect(() => {
    if (!link) {
      return;
    }
    formulaValidation.validate(`"${link}"`);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [link]);

  const updateTreeModel = useCallback(
    (
      targetNode: LinkedValueTreeNode | ((node: LinkedValueTreeNode) => boolean),
      updateTargetData: (node: LinkedValueTreeNode) => Partial<LinkedValueTreeNode>,
      updateOtherData?: (node: LinkedValueTreeNode) => Partial<LinkedValueTreeNode>,
    ) => {
      const mapToolsetTree = (tree: LinkedValueTreeNode[]): LinkedValueTreeNode[] => {
        return tree.map((node) => {
          const children = node.children?.length ? mapToolsetTree(node.children) : node.children;
          if (typeof targetNode === 'function' ? targetNode(node) : node === targetNode) {
            return { ...node, children, ...updateTargetData(node) };
          } else {
            return updateOtherData ? { ...node, children, ...updateOtherData(node) } : node;
          }
        });
      };
      setTreeNodes(mapToolsetTree);
    },
    [],
  );

  const onNodeClicked = useCallback(
    (node: LinkedValueTreeNode, toggled: boolean) => {
      updateTreeModel(
        node,
        (node) => ({
          active: true,
          toggled: node.children?.length ? toggled || !node.active : false,
        }),
        () => ({ active: false }),
      );
      setSelectedNode(node);
      setSelectedValueNode(null);
    },
    [updateTreeModel],
  );

  const handleValueSelect = useCallback(
    (node: LinkedValueTreeNode['values'][number]) => {
      updateTreeModel(
        () => true,
        () => ({ active: false }),
        undefined,
      );
      setSelectedValueNode(node);
    },
    [updateTreeModel],
  );

  return (
    <Dialog
      className="linked-value-select-dialog"
      title={<strong>{intl.messages['toolsetView.selectLinkedValue']}</strong>}
      onClose={onClose}
      open
      isPositionAbsolute
    >
      <div className="linked-value-select-dialog-main">
        <div className="top-info">
          <span>{intl.messages['toolsetView.highlightedNode']}:</span>&ensp;
          <strong>{link ?? '-'}</strong>
        </div>
        <div className="top-info">
          <span>{intl.messages['toolsetView.evaluatedValue']}:</span>&ensp;
          {formulaValidation.validating ? (
            <CircularProgress size="16px" />
          ) : validationError ? (
            <span className="error">{intl.messages['toolsetView.validationError']}</span>
          ) : (
            <strong>{formulaValidation.validationSuccessResult}</strong> ?? <span>-</span>
          )}
        </div>
        <div className="linked-value-select-dialog-body">
          <div>
            <Treebeard
              data={treeNodes}
              onToggle={(node, toggled) => onNodeClicked(node as LinkedValueTreeNode, toggled)}
              style={TreebeardStyle}
            />
          </div>
          <div className="values-list">
            {selectedNode?.values?.map((node) => (
              <div
                key={node.id}
                className={clsx(selectedValueNode === node && 'selected')}
                onClick={() => handleValueSelect(node)}
              >
                {node.name}
              </div>
            ))}
          </div>
        </div>
        <div className="options">
          <Button variant="outlined" color="primary" onClick={onClose}>
            {intl.messages['cancel']}
          </Button>
          <Button
            variant="outlined"
            color={validationError ? 'secondary' : 'primary'}
            disabled={!selectedNode && !selectedValueNode}
            onClick={() =>
              onSelect(parseInt(selectedValueNode?.id ?? selectedNode?.id ?? ''), link ?? '')
            }
          >
            {intl.messages['ok']}
            <ChevronDoubleLeftIcon
              className={clsx('chevron-double-button-icon', validationError && 'error')}
            />
          </Button>
        </div>
      </div>
    </Dialog>
  );
};
