import { useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import iconList from '../../assets/icons/overlay-list.svg';
import iconShowOff from '../../assets/icons/show-off.svg';
import iconShow from '../../assets/icons/show.svg';
import ActionBar, { ActionBarType } from '../../components/ActionBar';
import {
  initViewState,
  setViewData,
  setSaveFlag,
  updateListViewShowAliases,
} from '../../redux/actions/index';
import { TOGGLE_BUTTON } from '../../types/ActionBarType';

import { canReadListView, canEditListView } from '../../utils/permissions';
import { List } from './components/List';
import ValueSelector from './components/ValueSelector';

import 'react-base-table/styles.css';
import './style.scss';

interface ListViewProps {
  id: string;
  inspectionId: string;
}

const ListView = ({ id: viewId, inspectionId }: ListViewProps) => {
  const isSelectionMode = useSelector<any>(
    (state) => state.inspections[inspectionId]?.views[viewId]?.isSelectionMode ?? true,
  ) as boolean;
  const showAliases = useSelector<any>(
    (state) => state.inspections[inspectionId]?.sharedState?.showAliasesInList,
  ) as boolean;
  const myPermissions = useSelector<any>((state) => state.app?.myPermissions) as Set<string>;
  const dispatch = useDispatch();

  useEffect(() => {
    const initState = {
      selection: [],
      isSelectionMode: true,
    };

    const initSharedState = {};
    dispatch(initViewState(inspectionId, viewId, initState, initSharedState));

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [inspectionId, viewId]);

  const setAliasColumnVisibility = useCallback(
    (visibility: boolean) => {
      dispatch(updateListViewShowAliases(inspectionId, visibility));
      dispatch(setSaveFlag(true));
    },
    [dispatch, inspectionId],
  );

  const viewActions = useMemo<ActionBarType[]>(
    () => [
      {
        intlId: 'listView.save',
        iconUrl: iconList,
        onClickHandler: () => {
          dispatch(setViewData(inspectionId, viewId, 'isSelectionMode', !isSelectionMode));
          dispatch(setSaveFlag(true));
        },
        enabled: canEditListView(myPermissions),
      },
      {
        type: TOGGLE_BUTTON,
        id: inspectionId,
        intlId: 'listView.Actionbar.showAliases',
        iconUrl: iconShow,
        onClickHandler: () => {
          setAliasColumnVisibility(true);
        },
        intlIdAlternative: 'listView.Actionbar.hideAliases',
        iconUrlAlternative: iconShowOff,
        onClickHandlerAlternative: () => {
          setAliasColumnVisibility(false);
        },
        showAlternative: showAliases,
      },
    ],
    [
      dispatch,
      inspectionId,
      isSelectionMode,
      myPermissions,
      setAliasColumnVisibility,
      showAliases,
      viewId,
    ],
  );

  const canEdit = canEditListView(myPermissions);

  if (canReadListView(myPermissions) || canEdit) {
    return (
      <div className="list-view-wrapper">
        {isSelectionMode && canEdit ? (
          <ValueSelector viewId={viewId} inspectionId={inspectionId} />
        ) : (
          <List viewId={viewId} inspectionId={inspectionId} />
        )}
        <ActionBar isViewAction actions={viewActions} />
      </div>
    );
  }

  return <div></div>;
};

export default ListView;
